	module.exports = {"title":"10 Steps To Plan Better So You Can Write Less Code","order":5,"body":"<h2>Learn how to plan your future project!</h2>\n<p><em>Blog post written by BiancaMihai - <a href=\"https://github.com/biancamihai\">GitHub</a> / <a href=\"https://twitter.com/intent/user?screen_name=bubuslubu\">Twitter</a></em></p>\n<p>An ounce of preparation is worth a pound of cure. Thats true in medicine, and thats definitely true in software development.</p>\n<p>Heres a structured 10-step workflow that will guide you through the app planning process, with the goal of saving you from writing a lot of unnecessary code.</p>\n<p>Together, well plan out a simple To-do single-page web app. Well also plan for an API backend for a future mobile app.</p>\n<h3>1) Create our Trello board</h3>\n<p><a href=\"https://trello.com/\">Trello</a> is a fun, free way to break your planning and development process into small tasks that can be tracked.</p>\n<p><img src=\"https://lh3.googleusercontent.com/EI4AQ4NINm3B2DHR_YIS29JyKVa5dTPiT3RtITylmndFlpshTHepsKuO8_1KQNfdPDSBjslDReHCuPFeH1GNrDtgOwTyq6ZtGf3DFBmq1AsAhPHKt_0pLXQLf0o4ZbDuKVj4-Bo\" alt=\"Image of Trello board\"></p>\n<p><a href=\"https://trello.com/b/O9MZcYyY/todo-app\">Heres what our Trello board will eventually look like</a>. I prefer to split my tasks into 3 columns (depending on the complexity of the project):</p>\n<ul>\n<li>To Do - what is left to do</li>\n<li>In progress - tasks that people are currently working on</li>\n<li>Done - tasks that are done and ready for testing</li>\n</ul>\n<h3>2) Write user stories</h3>\n<p>Here are some example user stories. These will guide how we think about our apps features and functionality. Note that they all follow a similar structure: as a <person> I can <do something>.</p>\n<ul>\n<li>as a logged-in user I can see the list of my to-dos.</li>\n<li>as a logged-in user I can add a new to-do.</li>\n<li>as a logged-in user I can delete a to-do (only my to-dos - not other users).</li>\n<li>as a logged-in user I can complete a to-do (only my to-dos - not other users).</li>\n<li>as an anonymous user, I can register for a new account, recover my password, or log in to the app with an existing account.</li>\n</ul>\n<h3>3) Create our use case model</h3>\n<p>Our use case model will help us visualize our user stories so we can better understand how to implement them.</p>\n<p><img src=\"https://lh5.googleusercontent.com/2v6iIMbCrLSKVfqttEToum7OA3YGQCBKWUHcSCB1KEbEcijXxQtKJKY6fhLXeecJiO27P4icOuPlkVc9_uNXolzlzNXOo_TPh09GZsAqRH-JISqPrpx0PZdtbHOr0RIuQUbTbaw\" alt=\"Anonymous user case diagram\">\n<img src=\"https://lh6.googleusercontent.com/3V6dVvAcyjqFkaOukimucYOX0CfwBBYNN9SvjmnVy40Pdhs4Wtrr34i3E-9pbV7tFsp4jHm77IFQvFupjq6OWyxqEgCzcQ995Ayh52Msczu6TfwKeNhL9PYHyxSgmPYA1TR6l6Q\" alt=\"Authenticated user case diagram\"></p>\n<h3>4) Create our activity diagram</h3>\n<p>Our activity diagram will show the different paths our users can take through our app.</p>\n<p><img src=\"https://lh6.googleusercontent.com/jAQL4myqWOPA3gk2iTpGyAQBrO6p1GlPe8BQQ1Se6a-Di40X3Zw1p0wfJewZUL-YyDmedYzX5Lxvo2GW2Qnr6I-6kuKe1sDb9_5F_n46cKoawWReWW_ZoZCIJO6Semc4fvsiuHc\" alt=\"Activity diagram picture\"></p>\n<p>A user accesses our to-do application.</p>\n<ul>\n<li>If the user is not logged in she will see our login page.</li>\n<li>If she already has an account, she can log in.</li>\n<li>If she has an account, but she forgot her password, she can recover her password.</li>\n<li>If she doesnt have an account, she can create one.</li>\n<li>Both create an account and recover my password will require email validation. A user can log in to our application only after she has confirmed her email address.</li>\n<li>If she is logged in, she will see her to-do list (this can be empty if she hasnt added any to-dos yet).</li>\n<li>A logged in user:\n<ul>\n<li>is able to see her tasks list</li>\n<li>is able to mark a task from her list as completed</li>\n<li>is able to search within her task list</li>\n<li>is able to delete a task from her list</li>\n<li>can logout.</li>\n</ul>\n</li>\n<li>The user can exit the application at any time.</li>\n</ul>\n<h3>5) Create our mockups</h3>\n<p>Our mockups show what our app should look like. Its much faster to iterate on a mockup than it is to do so on working code.</p>\n<p><img src=\"https://lh3.googleusercontent.com/GBFhmBkfr-xM5YSXlR0Fm9y8b24ivdRlUtRWQOHJ8skNxEgjTkAef0e5nZ-CcHKNUq2p4V4hgDuAm9LSEuvbovlVborH1ZioAUXVlEblWZ4hN_d2tGEpxhfTkKH9os2JS1pab4w\" alt=\"Mockup picture\"></p>\n<h3>6) Choose the right technologies for our project</h3>\n<p>Because this is a single page app, well rely heavily - or in this case exclusively - on JavaScript. Lets use one of the most popular JavaScript stacks: the MEAN stack. One big benefit of the MEAN stack is that all of its components are free and open-source. There are also tons of resources available for learning the MEAN stack, and for debugging it when you inevitably encounter errors.</p>\n<p>You can have a <a href=\"http://www.freecodecamp.com/challenges/waypoint-get-set-for-basejumps\">MEAN stack development environment</a> up and running in the cloud in less than an hour, for free.</p>\n<p>Here are the components well use:</p>\n<ol>\n<li><a href=\"http://mongodb.org/\">MongoDB</a> for our database</li>\n<li><a href=\"http://nodejs.org/\">Node.js</a> and <a href=\"http://expressjs.com/\">Express.js</a> for implementing our API</li>\n<li><a href=\"http://angularjs.org/\">AngularJS</a>, along with HTML and CSS (and Bootstrap) for our client-side application</li>\n<li><a href=\"http://mongoosejs.com/\">Mongoose</a> to connect our application to MongoDB</li>\n</ol>\n<h3>7) Design our database schema</h3>\n<p>Its worth the effort to design a database schema, even for our simple application.</p>\n<p>Well have two collections: our Users collection will house our user data, and our ToDo collection will house our tasks that need to be done. One user can have zero, one, or many tasks in  her to-do list, so we will have a one-to-many (1:m) relationship between our two collections.</p>\n<p><img src=\"https://lh6.googleusercontent.com/5uSb_xnSSc5CWXJD0yyUGVJsL92RRZl3Bex_3wjuzl5Xr69Ks0j3od-yFju24SAd5wWMBNy9uqBrvOzdrUWluOkbcr4H5zFg-ZemJX3ZRWS12D42OowuvWnxA7wWIGrhhzaQ0aw\" alt=\"Database schema diagram\"></p>\n<h3>8) Define our use cases</h3>\n<ol>\n<li>What happens to the to-dos related to a user that deletes her account? When the user deletes her account the to-dos related to that user should also be deleted.</li>\n<li>No to-do can be added without being attached to a confirmed user.</li>\n<li>A to-do can only be deleted by its owner.</li>\n<li>No user can be added with an empty username or password.</li>\n<li>No to-do can be added with an empty task.</li>\n</ol>\n<p>Things to keep in mind:</p>\n<ol>\n<li>Use the Mongoose middleware to remove dependent documents like to-dos when a user deletes her account.</li>\n<li>Use Mongoose validation rules on models to prevent empty fields from being added to our database.</li>\n</ol>\n<h3>9) Design and test our API</h3>\n<p>I used a free product called Apiary <a href=\"http://docs.fcctodoapp.apiary.io/\">to document our API</a>.</p>\n<p>Heres the syntax I used to <a href=\"https://jsapi.apiary.io/apis/fcctodoapp.apib\">create this simple BluePrint</a>.</p>\n<p>I recommend you create an account and start playing with it. If you link your <a href=\"http://github.com/\">GitHub</a> account with Apiary, you can ensure your documentation always stays up to date. Youll also be able to test your data visually without the need for actually hitting your API endpoints. If you prefer to test your API from the command line, <a href=\"http://docs.agendor.apiary.io/\">heres an example of how to do this</a>.</p>\n<p>Later, once youve implemented your API with Node.js and Express.js, youll just need to set your URL in Apiary. Then you can start testing your calls. Our current host url (<a href=\"http://fcctodoapp.apiblueprint.org/\">http://fcctodoapp.apiblueprint.org/</a>) will be replaced by your APIs URL.</p>\n<p><img src=\"https://lh6.googleusercontent.com/hU3ilG_y9FqtL_zajQ_KOjWy8Qx590Go8nkNvA1j0oR50YJTpjJhL1lAPgjyeLTAS06tq6V62EcJrLQyT_TR2BK49DYiX6kksU6s9cqJDvvaS6jvepIM6uiO4JMbXuu-oXhdsas\" alt=\"App demo picture\"></p>\n<h3>10) Start writing code!</h3>\n<p>This is the fun part, and it will take up most of your projects time. If you need help with this, join us and learn to code.</p>\n"}

