	module.exports = {"title":"Creating a new API endpoint","order":5,"body":"<p>Your apps backend api that interacts with your database is located in <strong>/server/api</strong><br>\nLets take a look at <strong>/server/api/thing</strong>:</p>\n<ol>\n<li><strong>index.js</strong>:  this file routes the $http API requests made from your apps front-end to the appropriate function in **thing.controller.js **</li>\n<li><strong>thing.controller.js</strong>: Here is where we actually deal with the database! Take a minute to look through here and figure out whats going on. These functions will: return all items in a collection, return a single item from a collection when passed its id, post an item to a collection, update an item in the collection (this doesnt really work as intended out of the box, were going to fix that in a minute), and of course, delete an item from the collection.</li>\n<li><strong>thing.model.js</strong>: Here, the actual structure of a <em>thing</em> object is defined. You can add or remove any fields you want from the <em>thing</em> model, and as long as theyre syntactically correct they wont break anything, even if there are <em>things</em> with different schemas in your database already. But! You dont just have to edit the <em>thing</em> model to make a new type of collection, because generator-angular-fullstack can do it for you!</li>\n</ol>\n<p><a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/wiki/Grunt\">PREVIOUS</a>\n<a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/wiki/Creating-a-new-API-endpoint\">NEXT</a></p>\n"}

