	module.exports = {"title":"Creating a new directive","order":5,"body":"<p>Do you remember custom directives from the shaping up with angular course? You can also make a custom directive!</p>\n<pre><code>&gt;&gt; yo angular-fullstack:directive newdirective\n</code></pre>\n<p>And if you need an html template for your custom directive (maybe youre just making a directive to clean up your HTML code), tell it to make an html file when it prompts you to and youll be able to include the contents of that template anywhere in your app with:</p>\n<pre><code class=\"language-html\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-title\">newdirective</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-title\">newdirective</span>&gt;</span>\n</code></pre>\n<p><a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/wiki/Creating-a-new-route\">PREVIOUS</a>\n<a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/wiki/Grunt\">NEXT</a></p>\n"}

