	module.exports = {"title":"Welcome to the Free Code Camp Wiki!","body":"<p>Our open source communitys Wiki focuses on answering your questions about learning to code and getting a coding job. We also cover in detail our:</p>\n<ul>\n<li>Curriculum</li>\n<li>Local Campsite Communities</li>\n<li>Nonprofit Projects</li>\n</ul>\n<p><img src=\"http://i.imgur.com/gyJwzkx.png\" alt=\"An illustration of CamperBot\"></p>\n<p>The best way to access this wiki is via <a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/wiki/camperbot\">CamperBot</a>, located in our Gitter channels. CamperBot is a community designed tool useful for accessing our wiki content without ever leaving Gitter.</p>\n<p>To contribute a wiki article for any of the bonfires, please use this <a href=\"https://github.com/FreeCodeCamp/FreeCodeCamp/wiki/Bonfire-Wiki-Template\">template</a>. This is essential for <a href=\"https://github.com/camperbot\">@CamperBot</a> to retrieve the article. Bonfire articles that do not use this template will be removed.</p>\n<p>You will then need to clone the <a href=\"https://github.com/FreeCodeCamp/wiki\">wiki</a> and create a branch where you will make your changes and submmit a pull request to be merged before the changes goes live on the wiki. This is to filter any spam content or damage to <strong>our</strong> wiki.</p>\n<p>If you have questions about contributing to the Free Code Camp Wiki message <a href=\"https://gitter.im/Rafase282\">@Rafase282 in Gitter</a>.</p>\n<p>We look forward to collaborating with you on expanding and improving this wiki!</p>\n"}

